










































import { Component, Vue } from 'vue-property-decorator'
import { checkEmailValidation } from '@/utils/validation'
import AppButton from '@/components/UI/AppButton.vue'
import AppInput from '@/components/UI/AppInput.vue'

const LoginFormProps = Vue.extend({
  props: {
    submitForm: Function,
  },
})

@Component({
  components: {
    AppButton,
    AppInput,
  },
})
export default class LoginForm extends LoginFormProps {
  loginData = {
    email: '',
    password: '',
  }
  emailInfo = {
    type: '',
    text: '',
  }
  passwordInfo = {
    type: '',
    text: '',
  }
  isLoading = false

  submitButtonHandler() {
    this.emailValidation()
    this.passValidation()

    const validationSuccess = !this.emailInfo.type && !this.passwordInfo.type

    if (validationSuccess) {
      this.login()
    }
  }

  async login() {
    this.isLoading = true

    try {
      const res = await this.$store.dispatch(
        'moduleAuth/onLogin',
        this.loginData
      )
      if (res && res.status === 200) {
        this.submitForm(res.data.id)
      }
    } catch (error) {
      throw new Error()
    } finally {
      this.isLoading = false
    }
  }

  emailValidation() {
    const isEmailValid = checkEmailValidation(this.loginData.email.trim())
    if (!isEmailValid) {
      this.emailInfo = {
        type: 'error',
        text: "Email wasn't validated",
      }
    } else {
      this.emailInfo = {
        type: '',
        text: '',
      }
    }
  }

  passValidation() {
    if (this.loginData.password.length < 6) {
      this.passwordInfo = {
        type: 'error',
        text: 'Password length must be greater than 6 characters',
      }
    } else {
      this.passwordInfo = {
        type: '',
        text: '',
      }
    }
  }
}
